<template>
	<div class="pos">
			<el-row >
				<el-col :span='7' class='order-list'>
					<el-tabs>
						<el-tab-pane label='点餐' >
							 <el-table :data="tableData" border style='width: 100%'>
							 	 <el-table-column prop="goodsName" label='商品名称' ></el-table-column>
							 	 <el-table-column prop="count" label='数量'></el-table-column>
							 	 <el-table-column prop="price" label='金额' ></el-table-column>
							 	 <el-table-column  label='操作' fixed='right' width='100'>
							 <template slot-scope="scope">
					        <el-button type="text" size="small" @click="addolist(scope.row)">增加</el-button>
					        <el-button type="text" size="small" @click='dealist(scope.row)'>减少</el-button>
    		 				 </template>
							 	 </el-table-column>
							 </el-table>
							 <span>数量：</span>{{totalCount}}
							 <span>金额：</span>{{totalMoney}}
							 <div class="but-class">
							 	<el-button type="warning">挂单</el-button>
							 	<el-button type="danger" @click='deaAll'>取消</el-button>
							 	<el-button type="success">结账</el-button>
							 </div>
						</el-tab-pane>
						<el-tab-pane label='挂单'>
							
						</el-tab-pane>
						<el-tab-pane label='外卖'>
							
						</el-tab-pane>
					</el-tabs>
				</el-col>
				<el-col :span='17'>
					<div class="often-goods ">
						<div class="title">常用商品</div>
						<div class="often-goods-list">
							<ul>
								<li v-for="goods in oftenGoods" @click="addolist(goods)">
									<span>{{goods.goodsName}}</span>
									<span class="o-price">￥{{goods.price}}</span>
								</li>
							</ul>
						</div>
						<div class="goods-type">
							<el-tabs>
								<el-tab-pane label='汉堡'>
									<div class="clearfix">
										<ul class='cookList clearfix'>
										    <li v-for="goods in type0Goods" class="clearfix"  @click="addolist(goods)" >
										        <div class="foodImg"><img :src="goods.goodsImg" width="100%"></div>
										       <div style="float: left;">
										       	<p class="foodName">{{goods.goodsName}}</p>
										       	<p class="foodPrice">￥{{goods.price}}</p>
										       </div> 
										    </li>
										</ul>
									</div>
								</el-tab-pane>
								<el-tab-pane label='小吃'>
									<div class="clearfix">
										<ul class='cookList clearfix'>
										    <li v-for="goods in type1Goods" class="clearfix"  @click="addolist(goods)">
										        <div class="foodImg"><img :src="goods.goodsImg" width="100%"></div>
										       <div style="float: left;">
										       	<p class="foodName">{{goods.goodsName}}</p>
										       	<p class="foodPrice">￥{{goods.price}}</p>
										       </div> 
										    </li>
										</ul>
									</div>
								</el-tab-pane>
								<el-tab-pane label='饮料'>
									<div class="clearfix">
										<ul class='cookList clearfix'>
										    <li v-for="goods in type2Goods" class="clearfix"  @click="addolist(goods)">
										        <div class="foodImg"><img :src="goods.goodsImg" width="100%"></div>
										       <div style="float: left;">
										       	<p class="foodName">{{goods.goodsName}}</p>
										       	<p class="foodPrice">￥{{goods.price}}</p>
										       </div> 
										    </li>
										</ul>
									</div>
								</el-tab-pane>
								<el-tab-pane label='套餐'>
									<div class="clearfix">
										<ul class='cookList clearfix'>
										    <li v-for="goods in type3Goods" class="clearfix"  @click="addolist(goods)">
										        <div class="foodImg"><img :src="goods.goodsImg" width="100%"></div>
										       <div style="float: left;">
										       	<p class="foodName">{{goods.goodsName}}</p>
										       	<p class="foodPrice">￥{{goods.price}}</p>
										       </div> 
										    </li>
										</ul>
									</div>
								</el-tab-pane>
							</el-tabs>	
						</div>
					</div>
				</el-col>
			</el-row>
		
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'POS',
		data() {
        return {
          totalCount: 0,
		  totalMoney:0,
          tableData: [],
          oftenGoods:[],
          type0Goods:[],
          type1Goods:[],
          type2Goods:[],
          type3Goods:[]
        }
      },
      	created(){
      	axios.get('http://jspang.com/DemoApi/oftenGoods.php')
      	.then(reponse =>{
      		
      		this.oftenGoods = reponse.data;
      	})
      	.catch(error =>{
      		console.log(error)
      	})
      	
      	axios.get('http://jspang.com/DemoApi/typeGoods.php')
      	.then(reponse =>{
      		this.type0Goods = reponse.data[0];
      		this.type1Goods = reponse.data[1];
      		this.type2Goods = reponse.data[2];
      		this.type3Goods = reponse.data[3];
      	})
      	.catch(error =>{
      		console.log(error)
      	})
      },
		mounted(){
			let order = document.getElementsByClassName('order-list');
			let orderheight = document.body.clientHeight;
			order[0].style.height = orderheight+'px'	
		
		},
		methods:{
			addolist(goods){
				
				let flag = false;
				let lengs = this.tableData.length;
					
				//判断是否这个商品已经存在于订单列表
				for(let i=0;i<lengs;i++){
					if(this.tableData[i].goodsId==goods.goodsId){
                	  flag=true; //存在
                }
				}
//				根据flag来判断点菜单里有没有这道菜
				if(flag){
					//存在就进行数量添加
//					let arr = this.tableData.filter(function(o){return o.goodsId == goods.goodsId});
					let arr = this.tableData.filter(o =>o.goodsId == goods.goodsId);
					arr[0].count ++;
				}else{
					//不存在推入数组
					let newGoods = {goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1}
					this.tableData.push(newGoods)
				}
				this.totalMath();			
//				for(let i =0;i<lengs;i++){
//							this.totalCount += this.tableData[i].count;
//							this.totalMoney = this.totalMoney+(this.tableData[i].price*this.tableData[i].count)
//				}
			},
			//删除单条商品
			dealist(goods){ 
				//把菜单里的数据跟点击的数据id是否一致，存起来
				let arr = this.tableData.filter(o =>o.goodsId == goods.goodsId);
				let count = goods.count;
					arr[0].count --;
					if(count <=1){						
						this.tableData = 	this.tableData.filter(o =>o.goodsId != goods.goodsId)
					}
				this.totalMath();
			},
//			计算商品数量及价格
			totalMath(){
				this.totalCount = 0;
		  		this.totalMoney = 0;
		  		this.tableData.forEach((o) =>{
		  			this.totalCount += o.count;
		  			this.totalMoney = this.totalMoney+(o.price * o.count)
		  		})
			},
			//删除菜单所有的信息
			deaAll(){
				this.tableData = [];
				this.totalCount = 0;
		  		this.totalMoney = 0;
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos{
	font-size: 14px;
}
.order-list{
	background-color: #F9FAFC;
	border-right: 1px solid #C0CCDA;
	height: 100%;
}
.but-class{
	text-align: center;
	margin-top: 10px;
}
/*商品详情列*/
.title{
	height: 40px;
	border-bottom: 1px solid #D3dce6;
	background-color: #F9FAFC;
	padding: 10px;
	font-size: 14px;
}
.often-goods-list li{
	float: left;
	border: 1px solid  #E5E9F2;
	padding: 10px;
	margin: 10px;
	background-color: #FFF;
   		 cursor: pointer;
	
}

.o-price{
	color: #5807FF;
}
.goods-type{
	clear: both;
}
.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
   		 cursor: pointer;
   }
.clearfix:after {
  visibility: hidden;
  display: block;
  content: " ";
  clear: both; }

.clearfix {
  zoom: 1; }

.foodImg{
       width: 40%;
       float: left;
   }
.foodImg img{
    	display: block;
    }
.foodName{
       font-size: 16px;
       padding-left: 10px;
       color:brown;
   }
.foodPrice{
       font-size: 16px;
   	   padding-top: 5px;
       padding-left: 10px;	  
   }
   
</style>


